@import "../../_globalColor";

.main {
  width: 90%;
  padding: 20px 10px;
  margin: 0px auto;
  margin-top: 4rem;
  display: block !important;
  visibility: visible !important;
  opacity: 1 !important;
}

.achievement-main-div {
  display: flex;
  flex-direction: column;
  width: 100%;
  visibility: visible !important;
  opacity: 1 !important;
}

.achievement-header {
  margin-bottom: 40px;
  display: block !important;
  visibility: visible !important;
}

.achievement-heading {
  font-size: 56px;
  font-weight: 400;
  line-height: normal;
  margin: 0px;
}
.achievement-subtitle {
  text-transform: uppercase;
  margin: 0px;
  margin-bottom: 50px;
}
.subTitle {
  color: $subTitle;
}

/* Media Query */
@media (max-width: 1380px) {
  .achievement-heading {
    font-size: 40px;
  }
}
@media (max-width: 768px) {
  .main {
    width: 95%;
    padding: 10px 5px;
    margin-top: 2rem;
  }
  
  .achievement-heading {
    font-size: 30px;
    text-align: center;
  }
  .achievement-subtitle {
    font-size: 16px;
    line-height: normal;
    text-align: center;
    margin-bottom: 30px;
  }
}

@media (max-width: 480px) {
  .main {
    width: 100% !important;
    padding: 10px !important;
    margin-top: 1rem !important;
    display: block !important;
    visibility: visible !important;
  }
  
  .achievement-main-div {
    display: flex !important;
    flex-direction: column !important;
    width: 100% !important;
    visibility: visible !important;
  }
  
  .achievement-header {
    display: block !important;
    width: 100% !important;
    margin-bottom: 20px !important;
  }
  
  .achievement-heading {
    font-size: 24px !important;
    display: block !important;
    visibility: visible !important;
  }
  
  .achievement-subtitle {
    font-size: 14px !important;
    margin-bottom: 20px !important;
    display: block !important;
    visibility: visible !important;
  }
}
